.title {
    /* width: 50%; */
    display: flex;

    flex-direction: column;
    color: var(--sectionTitle);
    /* padding: 6rem 0 2rem 0; */
}
.titlebig {
    font-size: 2rem;
    font-weight: 500;
    padding-bottom: 0.4rem;
}
.titlesm {
    font-size: 1rem;
}
.bottomborder {
    width: 4rem;
    height: 3px;
    background-color: var(--sectionTitle);
}

.about {
    background-color: #ffffff;
    scroll-snap-align: start;
    margin-top: 5rem;
}

.layout {
    display: flex;
}
.layout > div {
    margin: 1rem;
    width: 100%;
}
.aboutdetails {
    padding: 1rem;
}

.aboutpara {
    font-weight: 200;
}
.cvbtn {
    display: flex;
    justify-content: center;
}
.cvbtn button {
    cursor: pointer;
    font-weight: 500;
    border: 2px solid gray;
    background-color: transparent;
    padding: 1rem 2rem;
    border-radius: 3px;
}
